.DiscoverList__img__warpper {
  text-align: center;
}

.react-multiple-carousel__arrow {
  border-radius: 35px;
  background: rgb(144 144 144 / 50%);
  min-width: 30px;
  min-height: 30px;
  z-index: 10 !important;
