html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  outline: none;
}

body {
  font-family: "Helvetica Neue", Arial, sans-serif;
  background-color: #f7f7f7;
}

#container {
  width: 550px;
  margin: 90px 160px;
}

#container * {
  margin-bottom: 50px;
  color: #383737;
}

#container h1 {
  width: auto;
  display: inline-block;
  font-size: 7em;
  line-height: 100%;
  letter-spacing: 0.2em;
  text-shadow: 8px -3px 0 rgba(240, 85, 85, 0.6),
    -10px 5px 0 rgba(72, 104, 244, 0.6), 9px 7px 0 rgba(255, 219, 89, 0.6);
  transition: all 0.2s ease;
}
#container h1:hover {
  text-shadow: 4px -1px 0 rgba(240, 85, 85, 0.6),
    -5px 2px 0 rgba(72, 104, 244, 0.6), 6px 3px 0 rgba(255, 219, 89, 0.6);
  transition: all 0.2s ease;
}

#container h3 {
  font-size: 2.5em;
}

#container a {
  padding: 15px 15px;
  color: white;
  font-weight: bold;
  font-size: 0.9em;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 0.09em;
  background-color: rgba(240, 85, 85, 0.9);
  box-shadow: -7px 7px 0 rgba(72, 104, 244, 0.7),
    -12px 12px 0 rgba(255, 219, 89, 0.8);
  transition: all 0.2s ease;
}
#container a:hover {
  box-shadow: -3px 3px 0 rgba(72, 104, 244, 0.7),
    -6px 6px 0 rgba(255, 219, 89, 0.8);
  transition: all 0.2s ease;
}

.ProductList__container {
  padding-bottom: 1rem;
}

.ProductPage__container {
  background-color: whitesmoke;
  height: 100%;
  margin: auto;
  display: flex;
  color: #444444;
  /* margin: 30px; */
  width: 100%;
  /* max-width: 900px; */
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: auto;
  /* margin: 2.5rem 0 5rem 0; */
  padding: 2.5rem 0.5rem 3.5rem 0.5rem;
  justify-content: flex-start;
  font-family: "Oswald", sans-serif;

}

.ProductPage__container h5 {
  text-transform: uppercase;
  margin: 0;
  font-size: 14px;
}

.ProductPage__container ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.ProductPage__container a {
  text-decoration: none;
  color: #bbb;
}

.ProductPage__product-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: linear-gradient(to bottom, #f3f0da 0%, #f3f3f3 100%);
  border-radius: 20px 20px 0 0;
  /* padding: 55px 0; */
  width: 100%;
  margin: 0;
}
.ProductPage__product-pic {
  max-width: 180px;
  position: relative;
  left: 0;
  margin: 40px 0;
  -webkit-filter: drop-shadow(-6px 40px 23px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(-6px 40px 23px rgba(0, 0, 0, 0.5));
  object-fit: contain;
}
.ProductPage__product-details {
  padding: 40px;
  background-color: white;
  border-radius: 0 0 20px 20px;
}
.ProductPage__product-details .title {
  text-transform: uppercase;
  margin: 0;
}
.ProductPage__product-details .ProductPage__colorCat {
  text-transform: uppercase;
  font-style: italic;
  color: #bbb;
  font-weight: 700;
  font-size: 14px;
}
.ProductPage__product-details .ProductPage__newPrice {
  font-weight: 500;
    margin-top: 5px;
    font-family: system-ui;
    font-size: 1.1rem;
}
.ProductPage__product-details .ProductPage__newPrice .ProductPage__current {
  color: #ef8f00;
  margin-left: 6px;
}
.ProductPage__product-details .ProductPage__oldPrice {
  text-decoration: line-through;
  color: #757575;
}
.ProductPage__product-details header {
  margin-bottom: 50px;
  position: relative;
}
.ProductPage__product-details article > h5 {
  margin: 0;
}
.ProductPage__product-details article > p {
  color: #bbb;
  margin: 0.5em 0;
  font-size: 14px;
  line-height: 1.6;
}
.ProductPage__product-details .ProductPage__controls {
  margin: 3em 0;
}
.ProductPage__product-details .ProductPage__controls > div {
  flex: 1 1;
}
.ProductPage__product-details .ProductPage__controls .option {
  margin-top: 12px;
  display: inline-block;
  position: relative;
}
.ProductPage__product-details .ProductPage__controls .ProductPage__option:hover {
  color: #444444;
}
.ProductPage__product-details .ProductPage__controls .ProductPage__option::before {
  content: "";
  position: absolute;
  border-width: 2px 2px 0 0;
  border-style: solid;
  top: 0;
  bottom: 0;
  height: 5px;
  width: 5px;
  right: -18px;
  margin: auto;
  transform: rotate(135deg);
}
.ProductPage__product-details .ProductPage__controls > div + div {
  margin-top: 20px;
  flex: none;
}
.ProductPage__product-details .ProductPage__controls ul {
  display: flex;
  margin: 15px 5px;
}
.ProductPage__product-details .ProductPage__color li + li {
  margin-left: 15px;
}
.ProductPage__product-details .ProductPage__colors {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: block;
}
.ProductPage__product-details .ProductPage__color-bdot1 {
  background-color: #59e8c8;
}
.ProductPage__product-details .ProductPage__color-bdot1:hover,
.ProductPage__product-details .ProductPage__color-bdot1.active {
  box-shadow: 0 0 0 3px white, 0 0 0 5px #59e8c8;
}
.ProductPage__product-details .ProductPage__color-bdot2 {
  background-color: #ffee71;
}
.ProductPage__product-details .ProductPage__color-bdot2:hover,
.ProductPage__product-details .ProductPage__color-bdot2.active {
  box-shadow: 0 0 0 3px white, 0 0 0 5px #ffee71;
}
.ProductPage__product-details .ProductPage__color-bdot3 {
  background-color: #6654af;
}
.ProductPage__product-details .ProductPage__olor-bdot3:hover,
.ProductPage__product-details .ProductPage__color-bdot3.active {
  box-shadow: 0 0 0 3px white, 0 0 0 5px #6654af;
}
.ProductPage__product-details .ProductPage__color-bdot4 {
  background-color: #343434;
}
.ProductPage__product-details .ProductPage__color-bdot4:hover,
.ProductPage__product-details .ProductPage__color-bdot4.active {
  box-shadow: 0 0 0 3px white, 0 0 0 5px #343434;
}
.ProductPage__product-details .ProductPage__color-bdot5 {
  background-color: #dfdfdf;
}
.ProductPage__product-details .ProductPage__color-bdot5:hover,
.ProductPage__product-details .ProductPage__color-bdot5.active {
  box-shadow: 0 0 0 3px white, 0 0 0 5px #dfdfdf;
}
.ProductPage__product-details .ProductPage__rate {
  position: static;
  margin-top: 10px;
}
.ProductPage__product-details .ProductPage__rate a {
  font-size: 18px;
  color: #bbb;
}
.ProductPage__product-details .ProductPage__rate a.active,
.ProductPage__product-details .ProductPage__rate a:hover {
  color: #fe6067;
}

.ProductPage__dots {
  display: flex;
  margin-top: 40px;
}
.ProductPage__dots > a {
  background-color: #349a98;
  width: 10px;
  height: 10px;
  margin: 0 4px;
  border-radius: 50%;
}
.ProductPage__dots > a:hover,
.ProductPage__dots > a.active {
  background-color: white;
}
.ProductPage__dots i {
  display: none;
}

.ProductPage__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2.5rem;
}
.ProductPage__footer > button {
  display: flex;
  border: 0;
  padding: 10px 15px;
  align-items: center;
  border-radius: 7px;
  cursor: pointer;
  background: linear-gradient(to bottom, #ffd173 0%, #f5b42e 100%);
  box-shadow: 3px 4px 7px 0px rgb(171 146 122 / 36%);
  transition: 200ms;
}
.ProductPage__footer > button:hover {
  background: linear-gradient(to bottom, #fdc659 0%, #f3a811 100%);
}
.ProductPage__footer > button > img {
  width: 20px;
}

.ProductPage__footer svg {
  color: #fff;
}
.ProductPage__footer > button > span {
  font-size: 16px;
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 10px;
  color: white;
}
.ProductPage__footer > a img {
  width: 20px;
  opacity: 0.8;
}
.ProductPage__footer > a:hover img {
  opacity: 1;
}

@media (min-width: 37.5em) {
  .ProductPage__product-details .rate {
    position: absolute;
    top: 12px;
    right: 10px;
    margin-top: 0;
  }
  .ProductPage__product-details .ProductPage__controls > div.qty {
    width: 60px;
  }
  .product-details .ProductPage__controls > div + div {
    border-left: 2px solid rgba(187, 187, 187, 0.5);
    padding-left: 25px;
    padding-right: 25px;
    width: 100px;
    margin-top: 0;
  }
  .ProductPage__product-details .ProductPage__controls {
    display: flex;
  }
}
@media (min-width: 56.25em) {
  .ProductPage__container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    margin: 0 0 0 2.5rem !important;
    justify-content: flex-start;
  }

  .ProductPage__product-image {
    border-radius: 20px 0 0 20px;
    max-width: 330px;
  }

  .ProductPage__product-pic {
    max-width: 250px;
  }

  .ProductPage__product-details {
    width: 100%;
    border-radius: 0 20px 20px 0;
    width: 55%;
  }
}
.snakerbar__wrapper .MuiPaper-root {
  background-color:#537290e3 !important;
}

.ProductPage__container .share svg {
  color: #909090
}
